.container {
    height: 100px;
    width: 30%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: center;
    margin-top: 20px;
}

.container>* {
    height: 50px;
}

.createButton {
    font-size: 30px;
    color: white;
    background: linear-gradient(135deg, rgb(0, 168, 89), rgb(104, 255, 104));
    border: 0;
    width: 40px;
    height: 40px;
    border-radius: 10px;
    cursor: pointer;
    transition: ease all 0.3s;
    outline: none;
}

.createButton:hover~.buttonLabel {
    display: block;
    opacity: 1;
}

.createButton:hover {
    background: linear-gradient(135deg, rgb(104, 255, 104), rgb(0, 168, 89));
    transform: scale(1.2);
    font-size: 32px;
}

.buttonLabel {
    font-size: 10px;
    width: 100px;
    top: 50px;
    left: -14px;
    position: absolute;
    transition: ease all 0.3s;
    opacity: 0;
}

.buttonContainer {
    position: relative;
}

@media (max-width: 500px) {
    .container {
        width: 100%;
    }
}