.container {
    display: grid;
    place-items: center;
    height: 100%;
}

.wrapper {
    display: grid;
    place-items: center;
    margin-top: -200px;
}

.title {
    color:rgb(0, 168, 89);
}

.input {
    width: 300px;
    height: 50px;
    border-radius: 30px;
    border: solid rgba(0,0,0,0.2) 1px;
    padding: 0 20px;
    outline: 0;
    margin: 50px 0;
}

.button {
    font-size: 20px;
    color: rgb(0, 168, 89);
    cursor: pointer;
    border-radius: 30px;
    transition: ease all 0.3s;
    border: 0;
    background: white;
    cursor: pointer;
    width: 200px;
    height: 50px;
    display: grid;
    place-items: center;
    margin-bottom: 20px;
}

.button:hover {
    background: rgb(0, 168, 89);
    color: white;
}