.container {
    height: 100px;
    background: url('./images/background.webp');
    position: relative;
}


.logo {
    height: 80px;
    left: 10%;
    background: url('./images/logo.webp') no-repeat;
    width: 200px;
    z-index: 100;
    position: absolute;
    top: 50%;
    margin-top: -40px;
}

.overlay {
    height: 100px;
    width: 100%;
    background-color: rgb(0, 168, 89);
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.95;
    z-index: 50;
}

.mediaButtons {
    position: absolute;
    right: 10%;
    width: 200px;
    display: flex;
    justify-content: space-evenly;
    height: 46px;
    top: 50%;
    margin-top: -23px;
    z-index: 100;
}

.mediaButtons > * {
    cursor: pointer;
    transition: ease all 0.3s;
}

.mediaButtons > *:hover {
    transform: scale(1.2);
}

.logoutButton, .logoutLabel {
    position: absolute;
    z-index: 100;
    color: white;
    font-size: 44px;
    top: 50%;
    right: 5%;
    margin-top: -28px;
    cursor: pointer;
    transition: ease all 0.3s;
}

.logoutButton:hover {
    transform: scale(1.2)
}

.logoutButton:hover ~ .logoutLabel{
    opacity: 1;
}

.logoutLabel {
    font-size: 5px;
    margin-top: 22px;
    opacity: 0;
    transition: ease all 0.3s;
    right: 5.1%;
}

@media (max-width: 800px) {
    .mediaButtons > * > * {
        width: 30px;
        height: 30px;
    }

    .logoutButton {
        font-size: 30px;
        top: 52px;
    }
}


@media (max-width: 500px) {
    .mediaButtons {
        display: none;
    }
}